<template>
	<div class="login-container">
		<div class="login-header">
			<p>欢迎登陆</p>
		</div>
		<div class="form-box">
			<Form ref="loginForm" :model="formData" :rules="ruleInline">
				<FormItem prop="username">
					<i-input type="text" v-model="formData.username" clearable size="large" placeholder="用户名">
						<Icon type="ios-person" slot="prepend"></Icon>
					</i-input>
				</FormItem>
				<FormItem prop="password">
					<i-input type="password" v-model="formData.password" clearable size="large" placeholder="密码">
						<Icon type="ios-lock-outline" slot="prepend"></Icon>
					</i-input>
				</FormItem>
				<FormItem>
					<Button type="error" size="large" @click="handleSubmit" long>登陆</Button>
				</FormItem>
			</Form>

			<Checkbox v-model="rememberPwd">记住密码</Checkbox>

			<router-link to="login/retrievePassword">
				<div class="reset-pwd">忘记密码？</div>
			</router-link>
		</div>
	</div>
</template>

<script>
	import CryptoJS from 'crypto-js'
	import {mapActions} from 'vuex';

	export default {
		name: 'LoginForm',

		data() {
			return {
				rememberPwd: true,

				formData: {
					username: '',
					password: ''
				},

				ruleInline: {
					username: [
						{required: true, message: '请输入用户名', trigger: 'blur'}
					],
					password: [
						{required: true, message: '请输入密码', trigger: 'blur'},
						{type: 'string', min: 6, message: '密码不能少于6位', trigger: 'blur'}
					]
				}
			};
		},

		mounted() {
			let tmp = localStorage.getItem('pwd');
			if (tmp) {
				tmp = JSON.parse(tmp);
				this.formData.username = tmp.username;
				this.formData.password = this.decryptPassword(tmp.password);
			}
		},

		methods: {
			...mapActions(['login']),

			encryptPassword(password) {
				return CryptoJS.DES.encrypt(CryptoJS.enc.Utf8.parse(password), CryptoJS.enc.Utf8.parse("FUckS@Lt"), {
					mode: CryptoJS.mode.CBC,
					padding: CryptoJS.pad.Pkcs7,
					iv: CryptoJS.enc.Utf8.parse("FUckS@Lt"),
				}).ciphertext.toString().toUpperCase();
			},

			decryptPassword(password) {
				password = CryptoJS.enc.Hex.parse(password);
				password = CryptoJS.enc.Base64.stringify(password);
				return CryptoJS.DES.decrypt(password, CryptoJS.enc.Utf8.parse("FUckS@Lt"), {
					mode: CryptoJS.mode.CBC,
					padding: CryptoJS.pad.Pkcs7,
					iv: CryptoJS.enc.Utf8.parse("FUckS@Lt"),
				}).toString(CryptoJS.enc.Utf8).toString();
			},

			handleSubmit() {
				this.$refs['loginForm'].validate(async (valid) => {
					if (valid) {
						let res = await this.login(this.formData);
						if (res) {
							if (this.rememberPwd) {
								let tmp = {
									username: this.formData.username,
									password: this.encryptPassword(this.formData.password),
								};
								localStorage.setItem('pwd', JSON.stringify(tmp));
							} else {
								localStorage.removeItem('pwd');
							}
							this.$Message.success('登陆成功');
							this.$router.push('/index');
						}
					} else {
						this.$Message.error('请填写正确的用户名或密码');
					}
				});
			}
		},
	};
</script>

<style scoped>
	.login-container {
		width: 80%;
		height: 300px;
		border: #ED3F14 solid 1px;
	}

	.login-header {
		height: 50px;
		font-size: 20px;
		text-align: center;
		line-height: 50px;
		letter-spacing: 5px;
		color: #fff;
		background-color: #ED3F14;
	}

	.form-box {
		width: 80%;
		margin: 30px auto;
	}

	.reset-pwd {
		float: right;
	}
</style>



